<template>
  <div>
    <h3 style="text-align: center; margin: 16px auto">{{ fileName }}</h3>
    <table id="customers">
      <tr v-for="(table, index) in this.tableData" :key="index">
        <th v-for="(data, index) in table" :key="index">{{ data }}</th>
      </tr>
    </table>
  </div>
</template>

<script>
import { GetFileExcel } from "../api/excelApi";
export default {
  data() {
    return {
      tableData: [],
      fileName: "",
      columns: [],
      rows: [],
    };
  },
  methods: {
    groupBy(xs, key) {
      return xs.reduce((rv, x) => {
        (rv[x[key]] = rv[x[key]] || []).push(x);
        return rv;
      }, []);
    },
    getDataExcel() {
      GetFileExcel({
        UrlCode: this.$route.params.id,
      }).then((res) => {
        if (res.RespCode == 0) {
          console.log(res);
          if (res.Columns.length == 0) {
            this.$notify({
              title: "Hết hạn",
              message: "Link excel đã hết hạn",
              type: "warning",
            });
          }
          this.fileName = res.FileName;
          this.columns = res.Columns;

          this.rows = this.groupBy(res.Rows, "RowIndex");
          this.rows[0] = this.columns;

          for (var i = 0; i < this.rows.length; i++) {
            if (i == 0) {
              var title = this.rows[i].map((item) => item.ColName);
              this.tableData.push(title);
            } else {
              var dataRow = this.rows[i].map((item) => item.RowValue);
              this.tableData.push(dataRow);
            }
          }
        } else {
          this.$notify.error({
            title: "Lỗi",
            message: res.RespText,
          });
        }
      });
    },
  },
  created() {
    this.getDataExcel();
    // console.log(this.$route.params.id);
  },
};
</script>

<style lang="scss" scoped>
#customers tr th {
  font-weight: 400;
  text-align: left;
  min-width: 250px;
  height: 40px;
}
#customers tr {
  min-width: 160px;
}
#customers tr:nth-child(1) {
  height: 40px;
  background-color: #04aa6d;
  color: white;
  font-size: 14px;
}
#customers tr:nth-child(even) {
  background-color: #f2f2f2;
}
#customers tr:hover {
  background-color: #ddd;
}
</style>
